*{
    padding: 0;
    margin: 0;
    font-family: $fuente_nav, sans-serif;
    box-sizing: border-box;
  }
  .dropdown-list{

    visibility: hidden;

    opacity: 0;
    height: 0;
    transition: visibility .3s ease-in-out, opacity .3s linear, height .3s linear;

    border-radius: 5%;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    justify-content: left;
    background-color: $blanco;

    a{
        margin-top: 1rem;
        font-size: 1.5rem;
        color: $grisOscuro;

        &:hover{
            color: $deepPurple;
            background-color: $grisClaro; 
        }
        }
    }

    .toggle{
        visibility: visible;
        opacity: 1;
        height: auto;
    }
    