@include tablet{
    .cart-interface{
        @include grid-columns(3,2rem);
        margin-top: 2rem;

        aside {
            grid-column: 2 / 3;
        }
    }
}

.cart-interface-object-quantity-buttons{
    display: flex;
    align-items: center;
    gap: 2rem;
    .cart-interface-object-quantity{
        font-size: 2rem;
        align-items: center;
        justify-content: center;
        text-align: center;
        background-color: $grisClaro;
        margin-top: 2rem;
    }

}

.cart-interface-object-price{
    font-family: $fuente_principal;
    font-size: 2rem;
    font-weight: 700;
}
.cart-interface-object-price2{
    font-family: $fuente_principal;
    font-size: 1.5rem;
    font-weight: 700;
    color: $grisOscuro;
}

.cart-interface-payment-detail{

    p{
        font-size: 2.5rem;
        font-family: $fuente_principal;
        font-weight: bold;
    }

    .cart-interface-payment-detail-value{
        font-weight: 100;
        font-family: $fuente_principal;
        justify-content: center;
        align-items: center;
        text-align: end;

    }
}

.cart-interface-payment-detail-valueTotal{
    color: $verde;
    font-weight: 700;
    font-family: $fuente_principal;
    justify-content: center;
    align-items: center;
    text-align: end;

}
