@include desktop{
    .user-dahsboard-grid{
        @include grid-columns(3,2rem);
        margin-top: 2rem;
    }

    .order-2-row-info{
        @include grid-columns(2,2rem);
        margin-top: 2rem;
        margin-bottom: 5rem;
    }
}

@include telefono{
    .orders-content{
        margin-top: 5rem;
    }
}
.user-dahsboard-grid{
    margin-top: 2rem;
}
.order-2-row-info{
}
.order-title{
    display: flex;
    gap: 5rem;
}

.orders-payment{
    color: $verde;
    font-family: $fuente_principal;
}
.order-2-row-info{
    border-bottom: 1px solid $grisOscuro;
}
.order-row-info{
    display: flex;
    flex-direction: column;
    justify-content:center;
    align-items: center;
}
.orders-lista{
    border-top: 1px solid $grisOscuro;
    border-left: 1px solid $grisOscuro;
    border-right: 1px solid $grisOscuro; 
    border-radius: 5%;
    background-color: $grisClaro;

    .order-opt-obj{
        display: block;
        padding: 1rem;
        border-bottom: 1px solid $grisOscuro;
        color: $grisOscuro;
    } & :hover{
        color: $azulOscuro;
    }
    a{
        font-size: 2rem;
    }
}

.order-info-list-productname{
    font-family: $fuente_nav;
    font-weight: 400;
    padding: 1rem;
}
.order-info-list-table{
width: 100%;
}

.user-photo-container{
    justify-content: center;
    background-color: $grisOscuro;
    border-radius: 50%;
    width: 8rem;
    height: 8rem;

}