.formulario{
    margin: 0 auto;
    padding: 2rem;
    border-radius: 1rem;

    fieldset{
        border: none;
    }

}
@include tablet{
    .form-contenedor-campos{
        @include grid(2, 1rem)
    }
}

.form-contenedor-campo{
    label{
        font-size: 2rem;
        font-family: $fuente_principal;
        color: $negro;
        margin-bottom: 0.5rem;
        display: block;
      }
    
      input{
        border: 1px solid $gris;
        width: 100%;
        padding: 1.5rem;
        border-radius: 0.5rem;
      }
      textarea{
        width: 100%;
        padding: 1.5rem;
        border-radius: 0.5rem;
        border: 1px solid $gris;
        
    }
}