/** Media Queries **/
@mixin telefono {
    //Original

    // @media (min-width: #{$telefono}) {
    //     @content;
    // }

    @media (max-width: #{$desktop}) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: #{$tablet}) {
        @content;
    }
}
  
@mixin desktop {
    @media (min-width: #{$desktop}) {
        @content;
    }
}
@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}

@mixin grid-columns($columnas, $espaciado ) {
    display: grid;
    column-gap: $espaciado;
    grid-template-columns:($columnas, 1fr);
    grid-auto-flow: column;

    aside{
        grid-column: 1 / 3;
    }
}


@mixin boton($bgColor, $display) {
    background-color: $bgColor;
    color: $blanco;
    text-decoration: none;
    padding: 1rem 3rem;
    text-align: center;
    margin-top: calc($separacion / 2);
    display: $display;
    border: none;
    border-radius: 1rem;

    &:hover {
        background-color: darken($bgColor, 5);
        cursor: pointer;
    }
}

@mixin botonClaro($bgColor, $display) {
    background-color: $bgColor;
    color: $grisOscuro;
    border: 1px solid $negro;
    text-decoration: none;
    padding: 1rem 3rem;
    text-align: center;
    margin-top: calc($separacion / 2);
    display: $display;
    border: none;
    border-radius: 1rem;

    &:hover {
        background-color: darken($bgColor, 20);
        cursor: pointer;
    }
}

@mixin boton-cuadrado($bgColor, $display, $height, $width ) {
    background-color: $bgColor;
    color: $blanco;
    font-weight: 400;
    text-decoration: none;
    padding: 1rem 3rem;
    height: $height;
    width: $width;
    text-align: center;
    margin-top: calc($separacion / 2);
    display: $display;
    border: none;

    &:hover {
        background-color: darken($bgColor, 5);
        cursor: pointer;
    }
}