}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  font-family: "Lato", sans-serif;
  color: #1E1E1E;
  font-size: 2rem;
}

.contenedor {
  width: 90%;
  max-width: 120rem;
  margin: 0 auto;
}

