  width: 70%;
  max-width: 120rem;
  margin: auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3, h4 {
  font-family: "Julius Sans One", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.logo {
  display: flex;
  justify-content: center;
  margin-bottom: 2.5rem;
}

.logo-ouroboros {
  height: 5rem;
  width: 8rem;
}
@media (min-width: 768px) {
  .logo-ouroboros {
    height: 3rem;
    width: 10rem;
  }
}

.dropdown-menu {
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 1000;
  display: none;
  float: left;
  min-width: 10rem;
  padding: 0.5rem 0;
  margin: 0.125rem 0 0;
  font-size: 1rem;
  color: #343a40;
  text-align: left;
  list-style: none;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 0.37rem;
}

