/** UI **/
.header {
  background-color: #1E1E1E;
  padding: 1rem 0 1rem 0;
}
@media (min-width: 768px) {
  .header {
    padding: 1rem 0 4rem 0;
  }
}

.contenido-header h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 2;
  max-width: 60rem;
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 1024px) {
  .derecha {
    align-items: flex-end;
  }
}

.barra {
  display: flex;
  flex-direction: row;
  gap: 45rem;
}
.navegacion {
  gap: 2rem;
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.3s linear, height 0.3s linear;
}
@media (min-width: 1024px) {
  .navegacion {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  font-family: "Julius Sans One", sans-serif;
  color: #D9D9D9;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
  text-transform: uppercase;
}
.navegacion a:hover {
  color: #1D267D;
  transition: 0.3s;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}

/*Burger Menu*/
.mobile-menu {
  padding-top: 1.5rem;
}
@media (min-width: 1024px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}
.mobile-menu:hover {
  cursor: pointer;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.nav-user-cart {
  justify-content: center;
  align-items: center;
  display: flex;
  gap: 1rem;
}

.footer {
  padding: 2.5rem 0;
  background-color: #1E1E1E;
  margin-bottom: 0;
}
.footer .contenedor-footer {
  padding-top: 2.5rem;
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
  height: auto;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}

@media (min-width: 1024px) {
  .contenedor-productos {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
  }
  .filter-bar {
    display: flex;
    align-items: center;
    gap: 2rem;
  }
  .contenedor-producto-detalle {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
  .contenedor-producto-detalle .precio-detalle {
    color: #666666;
    font-weight: 100;
    font-size: 3rem;
    text-align: left;
    justify-content: left;
  }
  .contenedor-producto-detalle h3, .contenedor-producto-detalle a {
    color: #1E1E1E;
    margin: 0;
    font-size: 3.5rem;
    text-align: left;
  }
  .contenedor-producto-detalle p {
    color: #b3b3b3;
    font-size: 2rem;
  }
}
@media (max-width: 1024px) {
  .contenedor-productos {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.contenedor-productos {
  margin-top: 5rem;
}

.producto {
  margin-bottom: 0;
  padding-bottom: 0;
}

.filter-bar p {
  color: #fafafa;
  font-weight: 100;
}

.producto {
  border-radius: 5%;
  background-color: #FFFFFF;
  margin-bottom: 3rem;
}
.producto .contenido-producto {
  padding: 0.5rem;
}
.producto h3, .producto a {
  color: #1E1E1E;
  margin: 0;
  font-size: 2rem;
  text-align: left;
}
.producto :hover {
  text-decoration: underline;
  cursor: pointer;
}
.producto span {
  font-family: "Julius Sans One", sans-serif;
  text-transform: uppercase;
}

.precio {
  color: gray;
  font-weight: 100;
  font-size: 2.5rem;
}

.contenedor-producto-detalle-carrito {
  display: flex;
  width: 50rem;
  gap: 2rem;
}
.contenedor-producto-detalle-carrito .producto-detalle-select-large {
  border: 0.5px solid #333333;
  width: 30rem;
  height: 5rem;
}
.contenedor-producto-detalle-carrito .producto-detalle-select-short {
  border: 0.5px solid #333333;
  width: 10rem;
  height: 5rem;
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.entrada-extra {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .entrada-extra {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.entrada-extra:last-of-type {
  margin-bottom: 0;
}

.informacion-meta span {
  color: #E08709;
}

.texto-entrada a {
  color: #1E1E1E;
  margin-top: 2rem;
  display: block;
}
@media (min-width: 768px) {
  .texto-entrada a {
    margin-top: 0;
  }
}
.texto-entrada h4 {
  margin: 0;
  line-height: 1.4;
}
.texto-entrada h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #3b7c1e;
  margin-top: 1rem;
}

.imagen {
  display: flex;
  justify-content: center;
  margin-bottom: 2.5rem;
}

.contenedor-colecciones-darkWall {
  background-image: url(/build/img/DarkWall.jpeg);
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
}
.contenedor-colecciones-darkWall h1, .contenedor-colecciones-darkWall h2, .contenedor-colecciones-darkWall h3, .contenedor-colecciones-darkWall p {
  color: #fafafa;
}

.colecciones {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
}

@media (min-width: 768px) {
  .colecciones {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.coleccion-cuadro {
  border: 0.5rem inset #E08709;
  border-radius: 2%;
  margin-bottom: 3rem;
  margin-top: 3rem;
  background-color: #fafafa;
}
.coleccion-cuadro span {
  font-family: "Julius Sans One", sans-serif;
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .banner-tallas {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
    margin-top: 2rem;
  }
}
.banner-tallas {
  margin-top: 2rem;
}
.banner-tallas picture {
  margin-top: 2rem;
}

@media (min-width: 1024px) {
  .user-dahsboard-grid {
    display: grid;
    column-gap: 2rem;
    grid-template-columns: 3, 1fr;
    grid-auto-flow: column;
    margin-top: 2rem;
  }
  .user-dahsboard-grid aside {
    grid-column: 1/3;
  }
  .order-2-row-info {
    display: grid;
    column-gap: 2rem;
    grid-template-columns: 2, 1fr;
    grid-auto-flow: column;
    margin-top: 2rem;
    margin-bottom: 5rem;
  }
  .order-2-row-info aside {
    grid-column: 1/3;
  }
}
@media (max-width: 1024px) {
  .orders-content {
    margin-top: 5rem;
  }
}
.user-dahsboard-grid {
  margin-top: 2rem;
}

.order-title {
  display: flex;
