}

.cart-interface-object-price2 {
  font-family: "Lato", sans-serif;
  font-size: 1.5rem;
  font-weight: 700;
  color: #333333;
}

.cart-interface-payment-detail p {
  font-size: 2.5rem;
  font-family: "Lato", sans-serif;
  font-weight: bold;
}
.cart-interface-payment-detail .cart-interface-payment-detail-value {
  font-weight: 100;
  font-family: "Lato", sans-serif;
  justify-content: center;
  align-items: center;
  text-align: end;
}

.cart-interface-payment-detail-valueTotal {
