  font-weight: 700;
  font-family: "Lato", sans-serif;
  justify-content: center;
  align-items: center;
  text-align: end;
}

/** Internas **/
.webp .banner {
  background-image: url(../img/.webp);
  height: 50rem;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  margin-bottom: 2rem;
}
.webp .banner .backgroun-image {
  height: 50rem;
  background-repeat: no-repeat;
  background-size: cover;
}

.no-webp .banner {
  background-image: url(../img/.jpg);
  height: 50rem;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  margin-bottom: 2rem;
}

.contenido-banner {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.2); /*Sintaxis para añadir transparencias*/
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.contenido-banner h2, .contenido-banner p {
  color: #FFFFFF;
  font-family: "Lato", sans-serif;
}

.contenedor-slider {
  position: absolute;
  width: 100%;
  height: 100%;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
  overflow: hidden;
}

.slides {
  position: relative;
  width: 100%;
  height: 100%;
}
.slides img {
  position: absolute;
  width: 50px;
  height: 50px;
  object-fit: cover;
  object-position: center;
  opacity: 0;
  transform: scale(0);
  transition: all 0.5s ease-in-out;
  transition-delay: 500ms;
}
.slides > [data-active] {
  opacity: 1;
  transform: scale(1);
  transition-delay: 0ms;
  width: 100%;
  height: 100%;
  z-index: 10;
}

.btn-slide {
  position: absolute;
  background: #D9D9D9;
  width: 40px;
  height: 40px;
  padding: 10px;
  border-radius: 50%;
  border: 1px solid rgba(34, 34, 34, 0.287);
  transition: opacity 300ms ease-in-out;
  cursor: pointer;
  overflow: hidden;
  z-index: 10;
}
@media (min-width: 768px) {
  .btn-slide {
    opacity: 0;
  }
}

.contenedor-slider:hover > .btn-slide {
  opacity: 1;
}

/* Previous and Next Button Position Absolute */
.prev,
.next {
  top: 50%;
  transform: translateY(-60%);
}

.prev {
  left: 20px;
}

.next {
  right: 20px;
}

/* Bottom Dots Container  */
.container-dots {
  position: absolute;
  bottom: 20px;
  width: 100%;
  display: flex;
  justify-content: center;
  z-index: 10;
}

/* Sigle Dot style */
.dot {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  border: 3px solid #f1f1f1;
  margin: 0 5px;
  background: #f1f1f1;
  cursor: pointer;
  transition: background-color 500ms ease-in-out;
}

/* Change the color of bg when user hover on it. */
.dot:hover {
  opacity: 0.9;
  background: rgb(32, 32, 32);
}

/* Current or active dot */
.dot[data-active=true] {
  background: rgb(32, 32, 32);
}

* {
  padding: 0;
  margin: 0;
  font-family: "Julius Sans One", sans-serif, sans-serif;
  box-sizing: border-box;
}

.dropdown-list {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.3s linear, height 0.3s linear;
  border-radius: 5%;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  justify-content: left;
  background-color: #FFFFFF;
}
.dropdown-list a {
  margin-top: 1rem;
  font-size: 1.5rem;
  color: #333333;
}
.dropdown-list a:hover {
  color: #1D267D;
  background-color: #fafafa;
}

.toggle {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.contenedor-search {
