.contenedor-colecciones-darkWall {
    background-image: url(/build/img/DarkWall.jpeg);
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;

    h1, h2, h3, p{
        
        color: $grisClaro;
    }
}
.colecciones{
    @include grid(2, 2rem);
    }
@include tablet {
    .colecciones{
    @include grid(3, 5rem);
    }
}


.coleccion-cuadro{
    border: 0.5rem inset $amarillo;
    border-radius: 2%;
    margin-bottom: 3rem;
    margin-top: 3rem;

    background-color: $grisClaro;
    span{
        font-family: $fuente_nav;
        text-transform: uppercase;
    }
}

